import {AtomicDesign} from '../../../../../Frontend/Core/AtomicDesign';
import {EVENTS} from '../../../../UiResources/events';

export default class PropertiesOrganism extends AtomicDesign {

    getBlockType(type){
        const block =  this.vue.selectedItems.blocks.find (block => block.name == type)
        return block?.data?.args0?.find(a=>a.type == "field_label_serializable")?.text;
    }

    blockSelected(selectedBlock){
        this.vue.selectedBlockProperties = null;
        if (selectedBlock){
            
            let definedProperties;
            
            if (selectedBlock.properties){
                this.vue.definedProperties = selectedBlock.properties;
                definedProperties = selectedBlock.properties;
            }else {
                const lessCodeBlock = this.vue.selectedItems.blocks.find(b=>b.name == selectedBlock.type);
                this.vue.definedProperties = lessCodeBlock.properties;
                definedProperties = lessCodeBlock.properties;
            }
          
            if (this.vue.selectedItems?.editable){
                this.vue.selectedEditable = this.vue.selectedItems.editable;
                let allPropertiesInEditable = this.vue.selectedEditable.properties;

                if (!allPropertiesInEditable){
                    this.vue.$set(this.vue.selectedEditable, "properties",{});
                    allPropertiesInEditable =this.vue.selectedEditable.properties;
                }
                
                let selectedBlockProperties = allPropertiesInEditable[selectedBlock.id];
                if (!selectedBlockProperties){
                    this.vue.$set(allPropertiesInEditable, selectedBlock.id, {});
                    selectedBlockProperties = allPropertiesInEditable[selectedBlock.id];
                }

                for(const name in definedProperties){
                    const propValue = selectedBlockProperties[name];
                    if (propValue == undefined || propValue == null){
                        let value = null;

                        const defaultValue = definedProperties[name]["default"];
                        if (defaultValue){
                            if (typeof (defaultValue) === "object" || Array.isArray(defaultValue) ){
                                value = JSON.parse(JSON.stringify(defaultValue));
                            }else {
                                value = defaultValue;
                            }
                        }

                        this.vue.$set(selectedBlockProperties, name, value);
                    }
                }
                
                this.vue.selectedBlockProperties = selectedBlockProperties;
            }
        }
    }

    filterEditables(editables,typeData){
        if (!editables){
            return [];
        }

        const {editableType} = typeData;

        if (typeof(editableType) === 'number'){
            return editables.filter(e=> e.type === editableType)
        }else {
            return editables.filter(e=> editableType.includes(e.type))
        }
    }

    filterSubSection(typeData){
        const {fieldRef, subSectionPath} = typeData;
        
        if (fieldRef){
            const refEditableId = this.vue.selectedBlockProperties[fieldRef];
            const refEditable = this.vue.openedDocument.editables.find(e=>e.id === refEditableId);

            if (refEditable){
                if (subSectionPath){
                    const pathParts = subSectionPath.split(".");
                    let currentObj = refEditable;
        
                    for(const part of pathParts){
                        currentObj = currentObj[part];
                        if (!currentObj) {
                            return [];
                        }
                    }
        
                    return currentObj;
                }
            }
        }


        return [];      
    }

    triggerPropertiesChanged(editable = null){
        editable = editable ? editable : this.vue.selectedEditable;
        this.emitGlobalEvent(EVENTS.DOCUMENT.ON_PROPERTIES_CHANGED,{editable});
    }

    editableSelected(key, value, field,selectedBlockProperties){
        const {typeData} = field;
        if (value == "..."){
            this.emitGlobalEvent(EVENTS.TEMPLATE.SHOW_DIALOG,{
                type:typeData.templateType,
                title:"Please select a template",
                field,
                callback : (data) => {
                    if (data){
                        this.templateSelected(data,key,selectedBlockProperties)
                    }else{
                        selectedBlockProperties[key] = null;
                        this.triggerPropertiesChanged();
                    }
                }
            });
        }else {
            this.triggerPropertiesChanged();
        }
    }

    emitEventDialog(dialog, key, value, field, selectedBlockProperties){
        this.emitGlobalEvent(dialog,{
            value,
            field,
            selectedBlockProperties,
            callback: (data)=>{
                if (data){
                    selectedBlockProperties[key] = data;
                    // this.actions().setOpenedDocument({result:this.vue.openedDocument});
                }
            }
        });
        
    }

    async templateSelected(data,key,selectedBlockProperties){
        const openedDocument = this.vue.$store.getters.openedDocument;
        const editable = await this.services("TemplateService").getRenderedTemplate(data,true);
        selectedBlockProperties[key] = editable.id;
        openedDocument.editables.push(editable);
        this.triggerPropertiesChanged(this.vue.selectedEditable);
        this.editEditable(key, editable.id);
    }

    editEditable(key,id){
        const openedDocument = this.vue.$store.getters.openedDocument;
        const editable = openedDocument.editables.find(e=>e.id == id);
        if (editable){
            this.actions().patchSelectedItems({editable})
        }
    }


    async deleteEditable(key,id,selectedBlockProperties){
        const result = await this.msgbox("Are you sure you want to delete this resource?", this.MSGBOX_TYPE.YES_NO,"Confirm Delete");

        if (result == this.MSGBOX_BUTTONS.YES){
            selectedBlockProperties[key] = null;
            const openedDocument = this.vue.$store.getters.openedDocument;
            const newEditables = openedDocument.editables.filter(e=>e.id != id);
            openedDocument.editables = newEditables;
            this.actions().setOpenedDocument({result:openedDocument});
        }
    }

    changeBlockCaption(value){
        const Blockly = require('blockly');
        if (Blockly.selected){
            Blockly.selected.setFieldValue(value,"CAPTION");
        }
    }
}