<template>
  <div class="entity-fields-molecule">
    <table class="entity-fields-molecule__table">
      <!-- <tr class="entity-fields-molecule__table__row">
        <th>Field Name</th>
        <th>Data Type</th>
      </tr> -->
      <tr class="entity-fields-molecule__table__row" v-for="field in propValue" :key="field.id">
          <td class="entity-fields-molecule__table__row__field" >
            <el-input size="mini" :disabled="typeof(field.dataType) ==='string' || field.primaryKey" v-model="field.name" :ref="field.id + '-key'"></el-input>
          </td>
          <td class="entity-fields-molecule__table__row__type" v-if="field.dataType ==='PARENTREF'">
            <label>Parent</label>
          </td>
          <td class="entity-fields-molecule__table__row__type" v-if="field.dataType !=='PARENTREF'">
            <el-select placeholder="" size="mini" v-model="field.dataType" v-on:change="dataTypeSelected(field)" :ref="field.id + '-value'" :filterable="true">
              <el-option v-for="dType in allDataTypes" :key="dType.id" :value="dType.id" :label="dType.caption"></el-option>
            </el-select>
          </td>
          <td class="entity-fields-molecule__table__row__toolbar" v-if="field.dataType !=='PARENTREF'">
            <el-popover placement="left" width="300" trigger="click">
              <table style="width:100%;">
                <tr>
                  <td><label>Name</label></td>
                  <td><el-input size="mini" v-model="field.name" :disabled="typeof(field.dataType) ==='string' || field.primaryKey"></el-input></td>
                </tr>
                <tr>
                  <td><label>Description</label></td>
                  <td><el-input size="mini" v-model="field.description"></el-input></td>
                </tr>
                <tr v-if="getDataType(field.dataType).hasSize">
                  <td><label>Size</label></td>
                  <td><el-input size="mini" v-model="field.size"></el-input></td>
                </tr>
                <tr v-if="schemaType == SCHEMA_TYPES.GENERIC">
                  <td><label>Minimum</label></td>
                  <td><el-input size="mini" v-model="field.minimum"></el-input></td>
                </tr>
                <tr v-if="schemaType == SCHEMA_TYPES.GENERIC">
                  <td><label>Maximum</label></td>
                  <td><el-input size="mini" v-model="field.maximum"></el-input></td>
                </tr>
                <tr v-if="typeof(field.dataType) !=='string' && !field.primaryKey">
                  <td><label>Default</label></td>
                  <td><el-input size="mini" v-model="field.default"></el-input></td>
                </tr>
                <tr v-if="typeof(field.dataType) ==='string'">
                  <td><label>Relationship</label></td>
                  <td>
                    <el-select placeholder="" size="mini" v-model="field.relationshipType" :filterable="true">
                      <el-option value="composition" label="Composition"></el-option>
                      <el-option :value="undefined"  label="Aggregation"></el-option>
                      <el-option value="association" label="Association"></el-option>
                    </el-select>
                  </td>
                </tr>
                <tr v-if="!field.primaryKey">
                  <td></td>
                  <td><el-checkbox v-model="field.required">Required</el-checkbox></td>
                </tr>
                <tr v-if="!field.primaryKey">
                  <td></td>
                  <td><el-checkbox v-model="field.isArray"  @change="changeFieldCaption(field)">Is Array</el-checkbox></td>
                </tr>
                <!-- <tr v-if="schemaType != SCHEMA_TYPES.GENERIC && (getDataType(field.dataType).canPrimaryKey || field.canPrimaryKey)"> -->
                <tr v-if="field.primaryKey">
                  <td>
                  </td>
                  <td><el-checkbox v-model="field.primaryKey" disabled>Is Primary Key</el-checkbox></td>
                </tr>
                <tr v-if="schemaType != SCHEMA_TYPES.GENERIC && (getDataType(field.dataType).canAutoIncrement || field.autoIncrement)">
                  <td></td>
                  <td><el-checkbox v-model="field.autoIncrement">Auto Increment</el-checkbox></td>
                </tr>
              </table>
              <el-button slot="reference"><img src="../../../../../../Frontend/public/assets/toolbar/pen-solid.svg" /></el-button>
            </el-popover>

            <button v-on:click="deleteField(field)">
              <img src="../../../../../../Frontend/public/assets/toolbar/trash-alt-solid.svg" />  
            </button>
          </td>
      </tr>
      <tr class="entity-fields-molecule__table__row">
          <td class="entity-fields-molecule__table__row__field" >
            <el-input size="mini" v-model="newFieldData.name" @change="createNewField('key',$event)" :ref="randomId" ></el-input>
          </td>
          <td class="entity-fields-molecule__table__row__type">
            <el-select placeholder="" v-model="newFieldData.dataType" size="mini" :filterable="true">
              <el-option v-for="dType in allDataTypes" :key="dType.id" :value="dType.id" :label="dType.caption"></el-option>
            </el-select>
          </td>
          <td class="entity-fields-molecule__table__row__toolbar-dummy">
            <div></div>
          </td>
      </tr>
    </table>
  </div>
</template>

<script>
import {uuid} from '../../../../../../Frontend/Helpers';
import {DATA_TYPES, SCHEMA_TYPES} from '../../../../../../Common/VariousTypes';
import EntityFieldsMolecule from './EntityFieldsMolecule.hook'
const hook = new EntityFieldsMolecule();
// import draggable from 'vuedraggable';

export default {
  name: 'EntityFieldsMolecule',
  beforeCreate() { 
    hook.hook(this)
  },
  components: { 
    // draggable
  },
  methods: {
    getDataType(dataTypeId){
      return this.allDataTypes.find(dt=>dt.id === dataTypeId) || {};
    },
    deleteField(field){hook.deleteField(this,this.selectedBlock.id,field) },
    dataTypeSelected(field) { 
      hook.dataTypeSelected(this, this.selectedBlock, field) 
    },
    changeFieldCaption(field){
      hook.changeFieldCaption(this,field);
    },
    createNewField(){
      if (this.propValue){

        let newField = {
          id : uuid(),
          name: this.newFieldData.name,
          dataType: 1
        };
        this.propValue.push(newField)
        
        this.newFieldData = {name: "", dataType:1};

        this.$nextTick(() => {
          let input = this.$refs[this.randomId];
          input.focus();        
        });

      }
    }
  },
  props: { 
    propKey:String,
    propValue:Array,
    schemaType:Number,
  },
  data() {
      return { 
        SCHEMA_TYPES,
        newFieldData: {
          name: "",
          dataType:1
        },
        randomId: `newfield-${uuid()}`
      }
  },
  computed: {
    allDataTypes(){
      return [...DATA_TYPES[this.schemaType],...this.customDataTypes];
    },
    ...hook.getters("customDataTypes","selectedBlock","selectedItems")
  }
}

</script>

<style scoped lang="scss">

.entity-fields-molecule {


  &__table {
    width:100%;
    &__row {
      
      th {
        text-align: left;
        padding-bottom:15px;
        font-size:13px;
        font-weight: 100;
        padding-left:4px;
      }
      &__field {
        width:50%;
      }

      &__type {
        width:50%;

        .el-input {
          font-size:14px;
        }

        label {
          padding-left:5px;
          font-weight: normal;
          font-size:13px;
        }
      }

      &__toolbar {
          display: flex;

          .el-button {
            padding:4px;
          }
          button {
            margin-left:5px;
            background-color: Transparent;
            border: none;
            cursor: pointer;
            img {
              height:16px;
            }
          }
      }

      &__toolbar-dummy {
        div {
          display: block;;
          width:53px;
        }
      }

    }
  }

  &__newfield:hover{
    opacity: 0.5;
  }

  &__newfield {
    transition: 0.3s;
    display: flex;
    text-align: center;
    width:100%;
    margin-top:15px;
    margin-bottom:20px;
    
    cursor: pointer;
    > * {
      cursor: pointer;
    }

    img {
      margin-left:80px;
      height:20px;
      margin-right:20px;
    }
    label {
      
    }
  }
}

</style>
