<template>
  <div class="properties-organism">
    <div class="properties-organism__container lc-custom-scroll">
      <table v-if="selectedBlock" class="properties-organism__table">
      
      <tr class="properties-organism__table__row" v-if="selectedBlock.type">
        <td class="properties-organism__table__row__label">
          <label>Type</label>
        </td>
        <td class="properties-organism__table__row__config"><label>{{blockTypeFromGuides}}</label></td>
      </tr>

      <tr v-for="(propValue,propKey) in definedProperties" :key="propKey"  class="properties-organism__table__row">
          
          <td class="properties-organism__table__row__label" v-if="propValue.type != 'entityFields' && propValue.type != 'title' && propValue.type != 'textArea' && propValue.type != 'dynamicFields'" >
            <el-tooltip v-if="propValue.tooltip" class="item" effect="dark" :content="propValue.tooltip" placement="left">
              <label>{{propValue.caption}}</label>
            </el-tooltip>
            <label v-if="!propValue.tooltip">{{propValue.caption}}</label>
          </td>

          <td class="properties-organism__table__row__config" v-if="propValue.type != 'entityFields' && propValue.type != 'title' && propValue.type != 'textArea' && propValue.type != 'dynamicFields'">
            
            <el-input v-if="propValue.type == 'text'" v-model="selectedBlockProperties[propKey]" size="mini"></el-input>
            <label v-if="propValue.type == 'label'" size="mini">{{propValue.typeData.caption}}</label>

            <el-select v-if="propValue.type == 'boolean'" placeholder="" size="mini" v-model="selectedBlockProperties[propKey]">
              <el-option :value="false" label="false"></el-option>
              <el-option :value="true" label="true"></el-option>
            </el-select>

            <el-input v-if="propValue.type == 'blockCaption'" v-model="selectedBlockProperties[propKey]"  @input="changeBlockCaption" size="mini"></el-input>
            
            <el-checkbox v-if="propValue.type == 'checkbox'" v-model="selectedBlockProperties[propKey]">{{selectedBlockProperties[propKey] ? 'Yes' : 'No'}}</el-checkbox>

            <div v-if="propValue.type == 'select'">
              <el-select v-if="propValue.typeData.options" placeholder="" size="mini" v-model="selectedBlockProperties[propKey]" :filterable="true" >
                <el-option v-for="option in propValue.typeData.options" :key="option" :value="option" :label="option"></el-option>
              </el-select>
            </div>

            <el-date-picker v-if="propValue.type == 'datetime'" v-model="selectedBlockProperties[propKey]" type="datetime" align="right" size="mini"></el-date-picker>
                       
            <div v-if="propValue.type == 'editable'" class="properties-organism__table__row__config__editable">
              <el-select placeholder="" size="mini" :filterable="true"
                v-model="selectedBlockProperties[propKey]" 
                v-on:change="editableSelected(propKey, selectedBlockProperties[propKey],propValue,selectedBlockProperties)">
                <el-option :value="null"></el-option>
                <el-option v-for="editable in filterEditables(openedDocument.editables,propValue.typeData)" :key="editable.id" :value="editable.id" :label="editable.properties['0'].name"></el-option>
                <el-option v-if="!propValue.typeData.hideCreateNew" value="...">[Create New]</el-option>
              </el-select>
              <!-- <button v-if="!propValue.typeData.hideToolbar" :disabled="!selectedBlockProperties[propKey]" v-on:click="editEditable(propKey,selectedBlockProperties[propKey],selectedBlockProperties)">
                <img src="../../../../../Frontend/public/assets/toolbar/pen-solid.svg" />
              </button>
              <button v-if="!propValue.typeData.hideToolbar" :disabled="!selectedBlockProperties[propKey]" v-on:click="deleteEditable(propKey,selectedBlockProperties[propKey],selectedBlockProperties)">
                <img src="../../../../../Frontend/public/assets/toolbar/trash-alt-solid.svg" />  
              </button> -->
            </div>

            <div v-if="propValue.type == 'editableSubSection'" class="properties-organism__table__row__config__editable">
              <el-select placeholder="" size="mini" v-model="selectedBlockProperties[propKey]" :filterable="true">
                <el-option :value="null"></el-option>
                <el-option v-for="ss in filterSubSection(propValue.typeData)" :key="ss[propValue.typeData.idField]" :value="ss[propValue.typeData.idField]" :label="ss[propValue.typeData.labelField]"></el-option>
              </el-select>
            </div>
            
            <div v-if="propValue.type == 'config'" class="properties-organism__table__row__config__config">
              <el-input v-if="selectedBlockProperties[propKey]" disabled v-model="selectedBlockProperties[propKey].name" size="mini"></el-input>
              <el-input v-if="!selectedBlockProperties[propKey]" disabled size="mini"></el-input>
              <el-button size="mini" @click="emitEventDialog(EVENTS.DIALOG.SHOW_CONFIGURATION, propKey, selectedBlockProperties[propKey],propValue,selectedBlockProperties)">...</el-button>
            </div>

            <div v-if="propValue.type == 'objectConstructor'" class="properties-organism__table__row__config__config">
              <el-input v-if="selectedBlockProperties[propKey]" disabled v-model="selectedBlockProperties[propKey].name" size="mini"></el-input>
              <el-input v-if="!selectedBlockProperties[propKey]" disabled size="mini"></el-input>
              <el-button size="mini" @click="emitEventDialog(EVENTS.DIALOG.SHOW_OBJECT_CONSTRUCTOR, propKey, selectedBlockProperties[propKey],propValue,selectedBlockProperties)">...</el-button>
            </div>

          </td>

          <td colspan="2" v-if="propValue.type == 'entityFields'">
            <EntityFieldsMolecule :propKey="propKey" :propValue="selectedBlockProperties[propKey]" :schemaType="propValue.typeData.schemaType" />
          </td>

          <td colspan="2" v-if="propValue.type == 'textArea'" class="properties-organism__table__row__text-area">
            <el-input v-model="selectedBlockProperties[propKey]" type="textarea" :rows="10"  class="lc-custom-scroll"></el-input>
          </td>

          <td colspan="2" v-if="propValue.type == 'dynamicFields'" class="properties-organism__table__row__dynamic">
            <DynamicFieldsAtom :fields="selectedBlockProperties[propKey]" :hideHeaders="true" :hideValues="propValue.typeData.hideValues" />
          </td>


          <td colspan="2" v-if="propValue.type == 'title'" class="properties-organism__table__row__title">
            <el-tooltip v-if="propValue.tooltip" class="item" effect="dark" :content="propValue.tooltip" placement="left">
              <label>{{propValue.caption}}</label>
            </el-tooltip>
            <label v-if="!propValue.tooltip">{{propValue.caption}}</label>
          </td>

      </tr>
        
      </table>
    </div>
  </div>
</template>

<script>
import {EVENTS} from '../../../../UiResources/events';
import PropertiesOrganism from './PropertiesOrganism.hook'
import EntityFieldsMolecule from './EntityFieldsMolecule/EntityFieldsMolecule';
import DynamicFieldsAtom from '../../../../UiCommon/Atoms/DynamicFieldsAtom/DynamicFieldsAtom';
const hook = new PropertiesOrganism();

export default {
  name: 'PropertiesOrganism',
  beforeCreate() { hook.hook(this)},
  methods: hook.methods("editableSelected","filterEditables","editEditable","deleteEditable","filterSubSection","emitEventDialog","changeBlockCaption"),
  components: {
    EntityFieldsMolecule,
    DynamicFieldsAtom
  },
  props: { },
  watch: {
    selectedBlock(val) { hook.blockSelected(val) }
  },
  data() {
      return { 
        definedProperties: null,
        selectedBlockProperties: null,
        selectedEditable: null,
        EVENTS
      }
  },
  computed: {
    availableVariables(){
        const selectedEditable = this.selectedItems.editable;
        if (selectedEditable){
          const editorService = this.$uiFramework?.services?.EditorService;
          if (editorService){
            return editorService.getAvailableVariables(selectedEditable);
          }
        }
       
        return [];
    },
    blockTypeFromGuides(){
      if (this.selectedBlock && this.guides){
        const guideObj = this.guides[this.selectedBlock.type];
        return guideObj?.info?.caption;
      }
      return "";
    },
    ...hook.getters("selectedBlock","guides","selectedItems","openedDocument")
  }
}

</script>

<style scoped lang="scss">


.properties-organism {
  padding-left:5px;
  padding-right:5px;
  height: calc(100vh - 120px);
  
  &__container {
    height: 100%;
    overflow-y: auto;
  }

  &__table{
    width:100%;
    border-spacing:7px; 

    &__header{ 
      background-color: rgb(71, 71, 71);
      th{
        width: 50%;
      }
    }

    &__row{ 
      
      &__text-area{
          margin-left:-100px;
          width:100%;
          height:30px;
      }

      &__title{
          font-size:14px;
          width:100%;
          height:30px;
      }

      &__label {
        width:30%;
        label {         
          font-size:14px;
          text-overflow: ellipsis;
          overflow: hidden; 
          white-space: nowrap;
        }
      }

      &__config{
        

        label {         
          font-size:14px;
          margin-left:15px;
          margin-bottom:5px;
          display: block;

        }

        &__config {
          width: 100%;
          display: flex;
          .el-input{
            width:auto;
            margin-right:10px;
          }

          .el-button {
            width:30px;
            padding-left:10px;
          }
        }

        .el-input {
          font-size:14px;
        }

        .el-date-editor.el-input, .el-date-editor.el-input__inner {
          width: 100%;
        }

        &__editable {
          display: flex;
          button {
            margin-left:2px;
            background-color: Transparent;
            border: none;
            cursor: pointer;
            img {
              height:16px;
            }
          }
        }

        &__variable {
          width:100%;
          display: flex;
          align-items: center;

          .el-input{
            width:auto;
          }

          label {
            margin-left:10px;
            width: auto;
          }
        }

      }

    }

  }
}

</style>
