<template>
  <div class="dynamic-fields-atom">
    <label class="dynamic-fields-atom__title">{{title}}</label>
    <table v-if="fields" class="dynamic-fields-atom__fields">
      <tr v-if="!hideHeaders">
        <th>Key</th>
        <th>Value</th>
      </tr>
      <tr v-for="field of fields" :key="field.id">
        <td><el-input size="small" v-model="field.key" :ref="field.id + '-key'"></el-input></td>
        <td v-if="!hideValues"><el-input size="small" v-model="field.value" :ref="field.id + '-value'"></el-input></td>
        <td><img src="../../../../Frontend/public/assets/toolbar/trash-alt-solid.svg" @click="deleteField(field)"/></td>
      </tr>
      <tr>
        <td><el-input size="small" v-model="newField.key" @change="createNewField('key',$event)" /></td>
        <td v-if="!hideValues" ><el-input size="small" v-model="newField.value" @change="createNewField('value',$event)"/></td>
        <td></td>
      </tr>
    </table>
    
  </div>
</template>

<script>

import {uuid} from '../../../../Frontend/Helpers/uuid';

export default {
  name: 'DynamicFieldsAtom',
  methods: { 
    deleteField(field){
      const id = this.fields.findIndex(f => f.id == field.id);
      if (id !== -1){
        this.fields.splice(id,1);
      }
    },
    createNewField(field, value){
      if (this.fields){
        let newField = {
          id : uuid(),
          key: (field == 'key' ? value : null),
          value: (field == 'value' ? value : null)
        };

        this.newField[field] = null
        this.fields.push(newField);

       this.$nextTick(() => {
         const oppositeField = field == "key" ? "value" : "key";
         let input = this.$refs[`${newField.id}-${oppositeField}`];
         if (input){
          if (input.length >0){
            input[0].focus();
          }else {
            input.focus();
          }
         }

         
      });

      }
    }
  },
  props: { 
    fields: Array,
    title:String,
    hideHeaders:Boolean,
    hideValues:Boolean
  },
  data() {
      return {
        newField : {
          key: null,
          value: null
        }
       }
  }
}

</script>

<style scoped lang="scss">

.dynamic-fields-atom {
  &__title {
    margin-left:5px;
  }

  &__fields {
    
    th {
      text-align: left;
      
      padding-bottom:10px;
    }

    td {
      padding-right:10px;
    }

    img {
      margin-top:5px;
      height:16px;
      cursor: pointer;
    }
  }
  
}

</style>
